<template>
	<view>
		<view class="header-area">
			<view class="user-box">
				<image src="@/static/img/user/set_up.png" mode="" class="setting-btn"></image>
				<u-avatar :src="user.avatar ? user.avatar : require('@/static/img/user/default.png')" size="60"></u-avatar>
				<view class="name-area" @click="doLogin">
					<view class="login-text">{{user.nickName ? user.nickName : '一键登录'}}</view>
					<view class="login-tips">{{user.nickName ? "新能源平台！" : '登录享受更多服务！'}}</view>
				</view>
			</view>
			
			<view class="grid-box">
				<u-grid :col="5" :border="false" @click="clickGrid" align="center">
					<u-grid-item v-for="(item, index) in menuList" :key="index">
						<img :src="item.icon" class="grid-image"></img>
						<text class="grid-text">{{item.name}}</text>
					</u-grid-item>
				</u-grid>
			</view>
		</view>
		
		<u-gap :height="5" bgColor="#fafafa"></u-gap>
		
		<view class="service-area">
			<view class="area-title">平台服务</view>
			<view class="area-menu">
				<u-grid :col="4" :border="false" align="center">
					<u-grid-item v-for="(item, index) in serviceMenuList" :key="index"  @click="clickService(item)">
						<img :src="item.icon" class="grid-image"></img>
						<text class="grid-text">{{item.name}}</text>
					</u-grid-item>
				</u-grid>
			</view>
			<view class="area-recommend">
				<view class="recommend-list">
					<view class="recommend-item">
						<view class="text-area">
							<view class="text">车品排行榜</view>
							<view class="tips">跟着车友买</view>
						</view>
						<img :src="require('@/static/img/user/recommend/ranking.png')">
					</view>
					<view class="recommend-item">
						<view class="name"></view>
						<view class="text-area">
							<view class="text">车主商城</view>
							<view class="tips">专车严选好物</view>
						</view>
						<img :src="require('@/static/img/user/recommend/mall.png')">
					</view>
				</view>
				
				<view class="recommend-list">
					<view class="recommend-item">
						<view class="text-area">
							<view class="text">汽车装饰</view>
							<view class="tips">香水 支架 车贴</view>
						</view>
						<img :src="require('@/static/img/user/recommend/decorate.png')">
					</view>
					<view class="recommend-item">
						<view class="name"></view>
						<view class="text-area">
							<view class="text">洗车卡</view>
							<view class="tips">洗车 19元/次</view>
						</view>
						<img :src="require('@/static/img/user/recommend/coupon.png')">
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			//用户信息
			user: {
				
			},
			//菜单
			menuList: [
				{ name: '收藏', icon: require('@/static/img/user/collection.png') },
				{ name: '足迹', icon: require('@/static/img/user/footprint.png') },
				{ name: '发布', icon: require('@/static/img/user/release.png') },
				{ name: '卡卷', icon: require('@/static/img/user/coupon.png') },
				{ name: '订单', icon: require('@/static/img/user/order.png') }
			],
			//服务菜单
			serviceMenuList:[
				{ name: '添加爱车', icon: require('@/static/img/user/service/add.png'), url: 'pages/member/my_car' },
				{ name: '查违章', icon: require('@/static/img/user/service/query.png') },
				{ name: '特惠加油', icon: require('@/static/img/user/service/oil.png') },
				{ name: '车主服务', icon: require('@/static/img/user/service/owner.png') },
			],
			
			//参数
			params: { },
		}
	},
	onShow(){
		let flag = this.checkLogin();
		if(flag){
			let vuex_user = uni.getStorageSync("vuex_user");
			let user = JSON.parse(vuex_user);
			this.user = user;
		}
	},
	onLoad() {
		
	},
	methods: {
		//检测登录
		checkLogin(){
			let vuex_token = uni.getStorageSync("vuex_token");
			if(!vuex_token){
				return false;
			}else{
				return true;
			}
		},
		//登录
		doLogin(){
			let flag = this.checkLogin();
			if(!flag){
				this.$u.route({ params: this.params, url: 'pages/member/login' });
			}
		},
		//点击服务
		clickService(item){
			this.$u.route({ params: this.params, url: item.url });
		},
	}
}
</script>

<style lang="scss">
page{
	background: #fafafa;
}
.header-area{
	background: #FFFFFF;
	height: 320rpx;
	.user-box{
		display: flex;
		padding: 30rpx 0 30rpx 30rpx;
		.setting-btn{
			width: 50rpx;
			height: 50rpx;
			position: absolute;
			top: 10rpx;
			right:40rpx;
		}
		.name-area{
			padding: 15rpx 0 0 25rpx;
			.login-text{
				padding: 0 0 10rpx 0;
			}
			.login-tips{
				color: #909399;
				font-size: 28rpx;
			}
		}
	}
	.grid-box{
		padding: 20rpx 0 0 0;
		.grid-image {
			width: 50rpx;
			height: 50rpx;
		}
		.grid-text {
			margin-top: 10rpx;
			letter-spacing: 2rpx;
			font-size: 20rpx;
		}
	}
}
.service-area{
	background: #FFFFFF;
	height: 480rpx;
	.area-title{
		padding: 20rpx 0 0 20rpx;
		font-size: 28rpx;
	}
	.area-menu{
		padding: 30rpx 0 0 0;
		.grid-image{
			width: 50rpx;
			height: 50rpx;
		}
		.grid-text {
			margin-top: 10rpx;
			letter-spacing: 2rpx;
			font-size: 20rpx;
		}
	}
	.area-recommend{
		padding: 30rpx 0 0 0;
		.recommend-list{
			padding: 0 20rpx 20rpx 20rpx;
			font-size: 24rpx;
			display: flex;
			justify-content: space-between;
			.recommend-item{
				background: #eeeeee;
				display: flex;
				width: 43%;
				height: 90rpx;
				padding: 20rpx 20rpx 0 20rpx;
				position: relative;
				.text-area{
					.text{
						font-size: 28rpx;
					}
					.tips{
						color: #909399;
					}
				}
				img{
					height: 60rpx;
					width: 60rpx;
					position: absolute;
					right: 20rpx;
				}
			}
		}
	}
}
</style>
